import { YourVideosService } from './../your-videos.service';
import { ReapplyComponent } from './../dialogs/reapply/reapply.component';
import { RejectComponent } from './../dialogs/reject/reject.component';
import { EditCourseComponent } from './../dialogs/edit-course/edit-course.component';
import { DeleteCourseComponent } from './../dialogs/delete-course/delete-course.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { environment } from '../../../../../environments/environment';
import { AppInitService } from 'src/app/service/app-init.service';

@Component({
  selector: 'app-videos',
  templateUrl: './videos.component.html',
  styleUrls: ['./videos.component.scss'],
})
export class VideosComponent implements OnInit {
  displayedColumns: string[] = [
    'video',
    'name',
    'category',
    'uploader',
    'length',
    'price',
    'status',
    'edit',
    'delete',
  ];

  courses: COURSE[];
  constructor(public dialog: MatDialog, public service: YourVideosService) {}

  ngOnInit(): void {
    this.coursesList();
  }
  coursesList() {
    this.service.listCourses().subscribe((res) => {
      let data = res.courses;
      console.log(data, res);
      this.courses = [];
      for (let file of data) {
        this.courses.push({
          id: file?._id,
          video: `${AppInitService.baseUrl}/${file?.file}`,
          name: file?.name,
          category: file?.category,
          uploader: file?.owner?.firstName + ' ' + file?.owner?.lastName,
          description: file?.description,
          price: file?.price,
          length: new Date(file?.length * 1000).toISOString().substr(14, 5),
          status: this.convertStatus(file.status),
          poster: `${AppInitService.baseUrl}/${file.thumbnail}`,
          notes: file?.notes ? file?.notes : [],
        });
      }
    });
  }
  openDialog(course) {
    console.log(course);
    const dialogRef = this.dialog.open(DeleteCourseComponent, {
      width: '400px',
      data: course,
    });

    dialogRef.afterClosed().subscribe((result) => {
      this.coursesList();
    });
  }
  openDialogEdit(video) {
    const dialogRef = this.dialog.open(EditCourseComponent, {
      width: '700px',
      data: video,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  openDialogReject() {
    const dialogRef = this.dialog.open(RejectComponent, {
      width: '400px',
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  openDialogReapply() {
    const dialogRef = this.dialog.open(ReapplyComponent, {
      width: '400px',
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  convertStatus(status): number {
    let st;
    switch (status) {
      case 'pending':
        st = 1;
        break;
      case 'approved':
        st = 2;
        break;
      case 'rejected':
        st = 3;
        break;
      case 'deleterequest':
        st = 4;
        break;
      case 'deleted':
        st = 5;
        break;
      default:
        st = 1;
        break;
    }
    return st;
  }
}

export interface COURSE {
  id: string;
  category: string;
  description: string;
  video: string;
  length: string;
  name: string;
  notes: [];
  uploader: string;
  price: number;
  status: number;
  poster: string;
}
